import React, { useState } from "react";
import { Button, Box, TextField, FormControl, Select, MenuItem, Chip, RadioGroup, FormControlLabel, Radio, FormLabel } from '@material-ui/core'
import { contents } from "../contents";

const defaultValues = {
    topic: '',
    contents: {
        subject: '',
        content: ''
    },
    students: {
        name: '',
        class: 0
    },
    date: '',
    duration: ''
}
console.log('defaultValues: ', defaultValues)

const LessonForm = () => {
    const [formValues, setFormValues] = useState(defaultValues)
    const [filteredContents, setFilteredContents] = useState((contents))

    console.log('formValues: ', formValues)
    console.log('filteredContents: ', filteredContents)

    const handleChange = (event) => {
      const {
        target: { name, value },
      } = event;
      console.log('name & value: ', name, value)
      if(name === 'subject') {
        if(value === 'maths') {
            setFilteredContents(contents.filter(c => c.tag === 'maths'))
            console.log('contents after if: ', contents)
        }
        else if(value === 'finnish') {
            setFilteredContents(contents.filter(c => c.tag === 'finnish'))
            console.log('contents after if: ', contents)
        }
        else if(value === 'religion') {
            setFilteredContents(contents.filter(c => c.tag === 'religion'))
            console.log('contents after if: ', contents)
        }
        else if(value === 'science') {
            setFilteredContents(contents.filter(c => c.tag === 'science'))
            console.log('contents after if: ', contents)
        } else {
            setFilteredContents(contents)
            console.log('went to else')
        }
      }
      setFormValues({
          ...formValues,
          contents: {
              ...formValues.contents,
              [name]: typeof value === 'string' ? value.split(',') : value
          }
      }
      );
    };

    const handleTextInputChange = (event) => {
        const {
          target: { name, value },
        } = event;
        console.log('name & value: ', name, value)
        setFormValues({
            ...formValues,
            [name]: value
        }
        );
      };

      const handleStudentsInputChange = (event) => {
        const {
          target: { name, value },
        } = event;
        console.log('name & value: ', name, value)
        setFormValues({
            ...formValues,
            students: {
                ...formValues.students,
                [name]: value
            }
        }
        );
      };

    const handleSubmit = (event) => {
        event.preventDefault()
        console.log(formValues)
    }

    return (
        <form onSubmit={handleSubmit} >
            <Box display='flex' flexDirection='column'>
                <Box display='flex' flexDirection='column' justifyContent='flex-start' padding='20px'>
                    <Box padding='20px' >
                        Topic
                        <TextField 
                            id='topic-input'
                            label=' '
                            autoFocus
                            required 
                            fullWidth
                            className='topic'
                            name='topic'
                            onChange={handleTextInputChange}                            
                        />
                    </Box>
                    <Box padding='20px' display='flex' flexDirection='column'>
                        Subject
                        <FormControl>
                            <Select
                                MenuProps={{
                                    anchorOrigin: {
                                    vertical: "bottom",
                                    horizontal: "left"
                                    },
                                    transformOrigin: {
                                    vertical: "top",
                                    horizontal: "left"
                                    },
                                    getContentAnchorEl: null
                                }}
                                value={formValues.contents.subject}
                                onChange={handleChange}
                                name='subject'
                            >
                                <MenuItem key='finnish' value='finnish'>
                                    Finnish
                                </MenuItem>
                                <MenuItem key='maths' value='maths'>
                                    maths
                                </MenuItem>
                                <MenuItem key='science' value='science'>
                                    science
                                </MenuItem>
                                <MenuItem key='religion' value='religion'>
                                    religion
                                </MenuItem>
                            </Select>
                        </FormControl>
                    </Box>
                    <Box padding='20px' display='flex' flexDirection='column'>
                        Content
                        <FormControl sx={{ m: 1, width: 300 }}>
                            <Select
                                MenuProps={{
                                    anchorOrigin: {
                                    vertical: "bottom",
                                    horizontal: "left"
                                    },
                                    transformOrigin: {
                                    vertical: "top",
                                    horizontal: "left"
                                    },
                                    getContentAnchorEl: null
                                }}
                                labelId="demo-multiple-chip-label"
                                id="demo-multiple-chip"
                                multiple
                                name='content'
                                value={formValues.contents.content ? formValues.contents.content : []}
                                onChange={handleChange}
                                renderValue={(selected) => (
                                    <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                                    {selected.map((value) => (
                                        <Chip key={value} label={value} />
                                    ))}
                                    </Box>
                                )}
                                >
                                {filteredContents.map((name) => (
                                    <MenuItem
                                        key={name.content}
                                        value={name.content}
                                    >
                                        {name.content}
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Box>
                    <Box padding='20px' display='flex' flexDirection='column'>
                        <FormControl>
                            Class
                            <RadioGroup
                                defaultValue='1'
                                name='class'
                                row
                                onChange={handleStudentsInputChange}
                            >
                                <FormControlLabel value='1' control={<Radio style= {{color:'#8bc34a'}} /> } label='1' />
                                <FormControlLabel value='2' control={<Radio style= {{color:'#8bc34a'}} /> } label='2' />
                                <FormControlLabel value='3' control={<Radio style= {{color:'#8bc34a'}} /> } label='3' />
                                <FormControlLabel value='4' control={<Radio style= {{color:'#8bc34a'}} /> } label='4' />
                                <FormControlLabel value='5' control={<Radio style= {{color:'#8bc34a'}} /> } label='5' />
                            </RadioGroup>
                        </FormControl>
                    </Box>
                    <Box padding='20px'>
                        Student
                        <TextField 
                            id='student-input'
                            label=' '
                            required 
                            fullWidth
                            className='student'
                            name='student'
                            onChange={handleStudentsInputChange}
                        />
                    </Box>
                </Box>
            </Box>
            <Box display='flex' justifyContent='center' paddingTop='20%'>
                <Button variant='contained' type='submit'>
                    Submit
                </Button>
            </Box>
        </form>
    )
    
}

export default LessonForm